      //document.onclickdocument
      document.onclick = function(event){
          //IE doesn't pass in the event object
          event = event || window.event;
          //IE uses srcElement as the target
          var target = event.target || event.srcElement;

          if (target.id == 'tip') {
              $.toast("ii")
          }
      };



